import React from "react";
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import ButtonToolbar from 'react-bootstrap/ButtonToolbar';
import { Card } from "react-bootstrap";
import {centerContent, errorButton, primaryButton, primaryCard, primaryCardTitle} from '../AppStyle';

const QuestionComponent = () => {
  return (
    <div style={centerContent}>
        <Card style={primaryCard}>
            <Card.Body>
                <Card.Title style={primaryCardTitle}>User Action Required</Card.Title>
                <Form>
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>HDFC Bank wants you to answer this security question: What as your child hood area pincode? (e.g.101010) </Form.Label>
                        <Form.Control type="text" placeholder="Enter answer here" />
                    </Form.Group>
                </Form>
                <ButtonToolbar>
                <Button variant="outline-secondary" style={primaryButton}>Submit</Button>
                    <Button variant="outline-error" style={errorButton}>Cancel</Button>
                </ButtonToolbar>
            </Card.Body>
        </Card>
    </div>
  );
};

export default QuestionComponent;